import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import JobTitleCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/employee/jobTitle/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/job-titles"

class CreateJobTitle extends Component {
  handleSubmit = value => {
    const { storeJobTitle } = this.props;
    storeJobTitle(value);
  };

  componentDidUpdate() {
    // Display error notif
    if (this.props.jobTitle.error) {
      notification("error", this.props.jobTitle.error);
      this.props.clearError();
    }

    if (this.props.jobTitle.success) {
      this.props.history.replace("/dashboard/setup/job-titles")
    }
  }

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="jobTitles.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <JobTitleCreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            jobTitle={this.props.jobTitle}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    jobTitle: state.jobTitle
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateJobTitle);
